<script>
import axios from "axios";
import { mapGetters } from "vuex";
export default {
  components: {},
  data() {
    return {
      loading: false,
      formData: {
        EXAM_ORG: '',
        EXAM_ORGCODE: '',
        EXAM_MGR: '',
        EXAM_MGR_CODE: '',
        EXAM_MGR_PHONE: '',
        EXAM_DATE: '',
        VERIFY_UNIT: '',
        VERIFY_UNIT_CODE: '',
        TEST_RUN_DAYS: '',
        VERIFY_DATE: '',
        APPR_AUTH: '',
        APPROV_DATE: '',
        APPR_NO: '',
        APPR_SIGNER: '',
        APPR_SIGNER_CODE: '',
        APPR_SIGN_PHONE: '',
      },
      rules: {
        APPR_AUTH: [
          {
            required: true,
            message: "请输入审批机关",
            trigger: "blur",
          },
        ],
        APPROV_DATE: [
          {
            required: true,
            message: "请选择审批日期",
            trigger: "change",
          },
        ],
        APPR_NO: [
          {
            required: true,
            message: "请输入取水许可审批文号",
            trigger: "blur",
          },
        ],
        APPR_SIGNER: [
          {
            required: true,
            message: "请输入签发人",
            trigger: "blur",
          },
        ],
      },
    };
  },
  computed: {
    ...mapGetters({
      allPageInfo: "waterForm/allPageInfo",
    }),
  },
  watch: {},
  created() {},
  mounted() {
    console.log(this.allPageInfo);
    this.formData = JSON.parse(JSON.stringify(this.allPageInfo?.EC_B_EXAMINE));
  },
  methods: {
    submitForm() {
      this.$refs["elForm"].validate((valid) => {
        if (!valid) return;
        this.loading = true;
        axios
          .post(
            `http://58.42.237.177:8975/pdcApi/fillIn/saveEcBExamine`,
            this.formData
          )
          .then(res => {
            if(res.data.success) {
              this.$message.success("保存成功");
            } else {
              this.$message.error(res.data.message);
            }
          })
          .finally(() => {
            this.loading = false;
          });
      });
    },
    resetForm() {
      this.$refs["elForm"].resetFields();
    },
  },
};
</script>
