<template>
  <div>
    <el-card>
      <div slot="header">
        <el-alert
          title="步骤7（申请人信息）"
          type="info"
          center
          show-icon
          :closable="false"
        >
        </el-alert>
      </div>
      <el-form ref="elForm" :model="formData" size="medium" label-width="120px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="申请人" prop="APPLICANT">
              <el-input
                v-model="formData.APPLICANT"
                clearable
                disabled
                :style="{ width: '100%' }"
              >
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="申请人代码" prop="APPL_CODE">
              <el-input
                v-model="formData.APPL_CODE"
                clearable
                disabled
                :style="{ width: '100%' }"
              >
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="申请人代码类型" prop="APPL_CODE_TYPE">
              <el-select
                v-model="formData.APPL_CODE_TYPE"
                placeholder="请选择"
                clearable
                disabled
                :style="{ width: '100%' }"
              >
                <el-option
                  v-for="(item, index) in allDictData.HOLDER_TYPE"
                  :key="index"
                  :label="item.name"
                  :value="item.code"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="申请日期" prop="APPLY_DATE">
              <el-date-picker
                v-model="formData.APPLY_DATE"
                format="yyyy-MM-dd"
                value-format="yyyy-MM-dd"
                :style="{ width: '100%' }"
                clearable
              ></el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="是否代理" prop="AGENT_FLAG">
              <el-select
                v-model="formData.AGENT_FLAG"
                placeholder="请选择"
                clearable
                :style="{ width: '100%' }"
              >
                <el-option
                  v-for="(item, index) in allDictData.IS_DICT"
                  :key="index"
                  :label="item.name"
                  :value="item.code"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="申请书编号" prop="APPL_NUM">
              <el-input
                v-model="formData.APPL_NUM"
                clearable
                :style="{ width: '100%' }"
              >
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item size="large" style="text-align: center">
          <el-button type="primary" @click="submitForm" :loading="loading">保存</el-button>
          <el-button @click="resetForm">重置</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>
<script>
import dayjs from "dayjs";
import axios from "axios";
import { mapGetters } from "vuex";
export default {
  components: {},
  data() {
    return {
      loading: false,
      formData: {
        APPLICANT: "",
        APPL_CODE: "",
        APPL_CODE_TYPE: "",
        APPLY_DATE: dayjs().format("YYYY-MM-DD"),
        AGENT_FLAG: "",
        APPL_NUM: "",
      },
    };
  },
  computed: {
    ...mapGetters({
      allPageInfo: "waterForm/allPageInfo",
      allDictData: "waterForm/allDictData",
    }),
  },
  watch: {},
  created() {},
  mounted() {
    console.log(this.allPageInfo);
    this.formData = JSON.parse(JSON.stringify(this.allPageInfo?.EC_B_APPLICANT));
  },
  methods: {
    submitForm() {
      this.$refs["elForm"].validate((valid) => {
        if (!valid) return;
        this.loading = true;
        axios
          .post(
            `http://58.42.237.177:8975/pdcApi/fillIn/saveEcBApplicant`,
            this.formData
          )
          .then(res => {
            if(res.data.success) {
              this.$message.success("保存成功");
            } else {
              this.$message.error(res.data.message);
            }
          })
          .finally(() => {
            this.loading = false;
          });
      });
    },
    resetForm() {
      this.$refs["elForm"].resetFields();
    },
  },
};
</script>
<style></style>
