!<template>
  <div class="water-form">
    <el-card>
      <div slot="header">
        <el-alert title="流程" type="info" center :closable="false"> </el-alert>
      </div>
      <div class="water-form__nav">
        <div class="water-form__btn">
          <el-button type="primary" size="mini" @click="onPrevStep"
            ><i class="el-icon-arrow-left"></i> 上一步</el-button
          >
          <el-button type="primary" size="mini" @click="onNextStep"
            >下一步 <i class="el-icon-arrow-right"></i
          ></el-button>
        </div>

        <el-steps
          direction="vertical"
          :active="active"
          finish-status="success"
          space="50px"
        >
          <el-step title="取水许可证照面信息"></el-step>
          <el-step title="取水权人基本信息"></el-step>
          <el-step title="取水工程信息"></el-step>
          <el-step title="用水监督"></el-step>
          <el-step title="供水监督"></el-step>
          <el-step title="退水监督"></el-step>
          <el-step title="申请人信息"></el-step>
          <el-step title="论证报告信息"></el-step>
          <el-step title="审批信息"></el-step>
          <el-step title="变更信息"></el-step>
          <el-step title="延续信息"></el-step>
        </el-steps>
      </div>
    </el-card>
    <div class="water-form__content" v-if="!pageLoading" :loading="pageLoading">
      <Setep1 v-if="active === 0"></Setep1>
      <Setep2 v-if="active === 1"></Setep2>
      <Setep3 v-if="active === 2"></Setep3>
      <Setep4 v-if="active === 3"></Setep4>
      <Setep5 v-if="active === 4"></Setep5>
      <Setep6 v-if="active === 5"></Setep6>
      <Setep7 v-if="active === 6"></Setep7>
      <Setep8 v-if="active === 7"></Setep8>
      <Setep9 v-if="active === 8"></Setep9>
      <Setep10Change v-if="active === 9"> </Setep10Change>
      <Setep10Continue v-if="active === 10"> </Setep10Continue>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import axios from "axios";
import toTree from "./utils";
import Setep1 from "./pages/step1";
import Setep2 from "./pages/step2";
import Setep3 from "./pages/step3";
import Setep4 from "./pages/step4";
import Setep5 from "./pages/step5";
import Setep6 from "./pages/step6";
import Setep7 from "./pages/step7";
import Setep8 from "./pages/step8";
import Setep9 from "./pages/step9";
import Setep10Change from "./pages/step10-change.vue";
import Setep10Continue from "./pages/step10-continue.vue";
export default {
  name: "water-form",
  components: {
    Setep1,
    Setep2,
    Setep3,
    Setep4,
    Setep5,
    Setep6,
    Setep7,
    Setep8,
    Setep9,
    Setep10Change,
    Setep10Continue,
  },
  // props: ['matterId'],
  data() {
    return {
      pageLoading: false,
      matterId: "37bfce447dd14a7396047481872169a2",
      active: 0,
    };
  },
  watch: {},
  computed: {
    ...mapGetters({
      allPageInfo: "waterForm/allPageInfo",
      allDictData: "waterForm/allDictData",
      areaList: "waterForm/areaList",
      industryList: "waterForm/industryList",
      waterList: "waterForm/waterList",
    }),
  },
  mounted() {
    this.getDictData();
    this.getAllPageInfo();
  },
  methods: {
    ...mapActions({
      setPageInfoA: "waterForm/setPageInfoA",
      setDictDataA: "waterForm/setDictDataA",
      setAreaListA: "waterForm/setAreaListA",
      setIndustryListA: "waterForm/setIndustryListA",
      setWaterListA: "waterForm/setWaterListA",
    }),
    onPrevStep() {
      if (this.active > 0) this.active -= 1;
    },
    onNextStep() {
      if (this.active < 10) this.active += 1;
    },
    getAllPageInfo() {
      this.pageLoading = true;
      axios
        .get(
          `http://58.42.237.177:8975/pdcApi/fillIn/getAllInfo?matterId=${this.matterId}`
        )
        .then((res) => {
          this.setPageInfoA(res.data.data);
        })
        .finally(() => {
          this.pageLoading = false;
        });
    },
    getDictData() {
      // 获取所有字典数据
      axios
        .get(`http://58.42.237.177:8975/pdcApi/dict/selectAllData`)
        .then((res) => {
          this.setDictDataA(res.data.data);
        });
      // 获取行政区划
      axios
        .post(`http://58.42.237.177:8975/pdcApi/common/queryCodeCommon`, {
          queryType: "1",
        })
        .then((res) => {
          this.setAreaListA(toTree(res.data.data));
        });
      // 水资源分区
      axios
        .post(`http://58.42.237.177:8975/pdcApi/common/queryCodeCommon`, {
          queryType: "2",
        })
        .then((res) => {
          this.setWaterListA(toTree(res.data.data));
        });
      // 行业类别
      axios
        .post(`http://58.42.237.177:8975/pdcApi/common/queryCodeCommon`, {
          queryType: "3",
        })
        .then((res) => {
          this.setIndustryListA(toTree(res.data.data));
        });
    },
  },
};
</script>
<style lang="scss" scoped>
.water-form {
  display: flex;
  width: 100%;
  &__btn {
    margin-bottom: 10px;
  }
  &__nav {
    flex-basis: 200px;
    height: 100%;
    padding: 0px 20px;
  }
  &__content {
    padding: 0px 10px;
    margin-left: 10px;
    flex: 1;
    // height: 600px;
    overflow: auto;
  }
}
</style>
