            :closable="false"
          >
          </el-alert>
        </div>
        <el-table
          :data="formData.SP_DSTS"
          border
          :header-cell-style="{ background: '#F3F4F7', color: '#555' }"
        >
          <el-table-column type="index" align="center" width="50" label="NO.">
          </el-table-column>
          <el-table-column prop="NAME" align="center" label="企业名称">
            <template slot-scope="scope">
              <el-input v-model="scope.row.NAME"></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="SCALE" align="center" label="用水规模">
            <template slot-scope="scope">
              <el-input v-model="scope.row.SCALE"
                ><template slot="append">万/m³</template></el-input
              >
            </template>
          </el-table-column>
          <el-table-column
            prop="IND_CLASS_CODE"
            align="center"
            label="行业类别"
          >
            <template slot-scope="scope">
              <el-cascader
                v-model="scope.row.IND_CLASS_CODE"
                clearable
                :options="industryList"
                :props="{ checkStrictly: true }"
                :style="{ width: '100%' }"
              >
              </el-cascader>
            </template>
          </el-table-column>
          <el-table-column
            fixed="right"
            align="center"
            label="操作"
            width="120"
          >
            <template slot-scope="scope">
              <el-button
                @click="deleteRow(scope.$index, formData.SP_DSTS)"
                type="text"
                size="small"
              >
                <i class="el-icon-delete"></i>
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <div style="text-align: center;margin-top:10px">
          <el-button type="primary" size="mini" @click="onAddTableData1"
            ><i class="el-icon-plus"></i>添加一个供水对象信息</el-button
          >
        </div>
      </el-card>
      <el-card style="margin: 10px 0;">
        <div slot="header">
          <el-alert
            title="填写供水区域信息"
            type="info"
            center
            show-icon
            :closable="false"
          >
          </el-alert>
        </div>
        <el-table
          :data="formData.SP_AREAS"
          border
          :header-cell-style="{ background: '#F3F4F7', color: '#555' }"
        >
          <el-table-column type="index" align="center" width="50" label="NO.">
          </el-table-column>
          <el-table-column prop="SP_AREA" align="center" label="供水区域">
            <template slot-scope="scope">
              <el-input v-model="scope.row.SP_AREA"></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="SP_WAT_AMNT" align="center" label="年供水量">
            <template slot-scope="scope">
              <el-input v-model="scope.row.SP_WAT_AMNT"
                ><template slot="append">万/m³</template></el-input
              >
            </template>
          </el-table-column>
          <el-table-column
            fixed="right"
            align="center"
            label="操作"
            width="120"
          >
            <template slot-scope="scope">
              <el-button
                @click="deleteRow(scope.$index, formData.SP_AREAS)"
                type="text"
                size="small"
              >
                <i class="el-icon-delete"></i>
              </el-button>
            </template>
          </el-table-column>
