            show-icon
            :closable="false"
          >
          </el-alert>
        </div>
        <el-table
          :data="formData.SP_USAGES"
          border
          :header-cell-style="{ background: '#F3F4F7', color: '#555' }"
        >
          <el-table-column type="index" width="50" label="NO.">
          </el-table-column>
          <el-table-column prop="SP_TYPE_CODE" align="center" label="供水类型">
            <template slot-scope="scope">
              <el-select
                v-model="scope.row.SP_TYPE_CODE"
                placeholder="请选择"
                clearable
                :style="{ width: '100%' }"
              >
                <el-option
                  v-for="(item, index) in allDictData.WAT_USAGE_DES"
                  :key="index"
                  :label="item.name"
                  :value="item.code"
                ></el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column prop="SP_TYPE" align="center" label="具体用途">
            <template slot-scope="scope">
              <el-input v-model="scope.row.SP_TYPE"></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="SP_WAT_AMNT" align="center" label="年供水量">
            <template slot-scope="scope">
              <el-input v-model="scope.row.SP_WAT_AMNT">
                <template slot="append">万/m³</template>
              </el-input>
            </template>
          </el-table-column>
          <el-table-column
            fixed="right"
            align="center"
            label="操作"
            width="120"
          >
            <template slot-scope="scope">
              <el-button
                @click="deleteRow(scope.$index, formData.SP_USAGES)"
                type="text"
                size="small"
              >
                <i class="el-icon-delete"></i>
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <div style="text-align: center;margin-top:10px">
          <el-button type="primary" size="mini" @click="onAddTableData3"
            ><i class="el-icon-plus"></i>添加一个供水类型信息</el-button
          >
        </div>
      </el-card>
      <div style="text-align: center;margin-top:10px">
        <el-button type="primary" @click="submitForm" :loading="loading"
          >保存</el-button
        >
      </div>
    </el-card>
  </div>
</template>

<script>
import axios from "axios";
import { mapGetters } from "vuex";
export default {
  name: "step5",
  components: {},
  data() {
    return {
      loading: false,
      formData: {
        TOT_AMNT: "",
        WT_QUA_STA: "",
        WT_CT_IDX: "",
        SP_DSTS: [],
        SP_AREAS: [],
        SP_USAGES: [],
      },
    };
  },
  watch: {},
  computed: {
    ...mapGetters({
      allPageInfo: "waterForm/allPageInfo",
      allDictData: "waterForm/allDictData",
      industryList: "waterForm/industryList",
    }),
  },
  mounted() {
    console.log(this.allPageInfo);
    this.formData = JSON.parse(
      JSON.stringify(this.allPageInfo?.EC_B_SUPPORT_CT)
    );
  },
  methods: {
    submitForm() {
      this.loading = true;
      axios
        .post(
          `http://58.42.237.177:8975/pdcApi/fillIn/saveEcBSupportCt`,
          this.formData
        )
        .then((res) => {
          if (res.data.success) {
            this.$message.success("保存成功");
          } else {
            this.$message.error(res.data.message);
          }
        })
        .finally(() => {
          this.loading = false;
        });
    },
    onAddTableData1() {
      if (!this.formData.SP_DSTS) {
        this.$set(this.formData, "SP_DSTS", []);
      }
      this.formData.SP_DSTS.push({
        NAME: "",
        SCALE: "",
        IND_CLASS_CODE: [],
        EC_B_SUPPORT_CT_ID: this.formData.ID,
      });
    },
    onAddTableData2() {
      if (!this.formData.SP_AREAS) {
        this.$set(this.formData, "SP_AREAS", []);
      }
      this.formData.SP_AREAS.push({
        SP_AREA: "",
        SP_WAT_AMNT: "",
        EC_B_SUPPORT_CT_ID: this.formData.ID,
      });
    },
    onAddTableData3() {
      if (!this.formData.SP_USAGES) {
        this.$set(this.formData, "SP_USAGES", []);
      }
      this.formData.SP_USAGES.push({
        SP_TYPE_CODE: "",
        SP_WAT_AMNT: "",
        SP_TYPE: "",
        EC_B_SUPPORT_CT_ID: this.formData.ID,
      });
    },
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },
  },
};
</script>
<style lang="scss" scoped></style>
