<script>
import axios from "axios";
import { mapGetters } from "vuex";
export default {
  components: {},
  data() {
    return {
      loading: false,
      formData: {
        PART_OWNERS: [
          // {
          //   NAME: "",
          //   SHARE: "",
          //   CODE: "",
          //   CODE_TYPE: "",
          // },
        ],
        WAT_RIG_OWNER: "",
        OWNER_CODE: "",
        OWNER_CODE_TYPE: "",
        IND_CLASS_CODE: "",
        POSTAL_CODE: "",
        REG_ADDR: "",
        OP_LOC_DIV_CODE: "",
        OP_LOCATION: "",
        CON_PER: "",
        CELL_PHONE: "",
        PRO_NAT_CODE: "",
        PRO_NAME: "",
      },
      rules: {
        WAT_RIG_OWNER: [
          {
            required: true,
            message: "取水权人名称不能为空",
            trigger: "blur",
          },
        ],
        OWNER_CODE: [
          {
            required: true,
            message: "取水权人代码不能为空",
            trigger: "blur",
          },
        ],
        OWNER_CODE_TYPE: [
          {
            required: true,
            message: "取水权人代码类型不能为空",
            trigger: "change",
          },
        ],
        IND_CLASS_CODE: [
          {
            required: true,
            message: "行业类型不能为空",
            trigger: "blur",
          },
        ],
        POSTAL_CODE: [
          {
            required: true,
            message: "邮编不能为空",
            trigger: "blur",
          },
        ],
        REG_ADDR: [
          {
            required: true,
            message:
              "单位填写市场监管部门登记的注册地址；个人填写身份证载明的住址!",
            trigger: "blur",
          },
        ],
        OP_LOC_DIV_CODE: [
          {
            required: true,
            message: "请选择生产经营所在地的县级行政区划代码",
            trigger: "change",
          },
        ],
        OP_LOCATION: [
          {
            required: true,
            message: "生产经营场所地址,包括具体街道、小区门牌号!",
            trigger: "blur",
          },
        ],
        CON_PER: [
          {
            required: true,
            message: "请输入联系人",
            trigger: "blur",
          },
        ],
        CELL_PHONE: [
          {
            required: true,
            message: "请输入联系人手机号",
            trigger: "blur",
          },
        ],
      },
    };
  },
  computed: {
    ...mapGetters({
      allPageInfo: "waterForm/allPageInfo",
      allDictData: "waterForm/allDictData",
      areaList: "waterForm/areaList",
      industryList: "waterForm/industryList",
    }),
  },
  watch: {},
  created() {},
  mounted() {
    this.formData = JSON.parse(JSON.stringify(this.allPageInfo?.EC_B_WATER_RIG_OWNER));
  },
  methods: {
    onAddTableData() {
      if (!this.formData.PART_OWNERS) {
        this.$set(this.formData,"PART_OWNERS", [])
      }
      this.formData.PART_OWNERS.push({
        NAME: "",
        SHARE: "",
        CODE: "",
        CODE_TYPE: "",
      });
    },
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },
    submitForm() {
      console.log(this.formData, "formData");
      this.$refs["elForm"].validate((valid) => {
        if (!valid) return;
        // TODO 提交表单
        this.loading = true;
        this.formData.MATTER_ID = this.allPageInfo.MATTER_ID;
        axios
          .post(
            `http://58.42.237.177:8975/pdcApi/fillIn/saveEcBWaterRigOwner`,
            this.formData
          )
          .then(res => {
            if(res.data.success) {
              this.$message.success("保存成功");
            } else {
              this.$message.error(res.data.message);
            }
          })
          .finally(() => {
            this.loading = false;
          });
      });
    },
    resetForm() {
      this.$refs["elForm"].resetFields();
    },
  },
};
</script>
