<template>
  <div>
    <el-card>
      <div slot="header">
        <el-alert
          title="步骤10（变更信息）"
          type="info"
          center
          show-icon
          :closable="false"
        >
        </el-alert>
      </div>
      <el-form
        ref="elForm"
        :model="formData"
        :rules="rules"
        size="medium"
        label-width="210px"
      >
        <el-col :span="24">
          <el-form-item label="原证照编号" prop="CERT_NUM_CODE">
            <el-input
              v-model="formData.CERT_NUM_CODE"
              placeholder="请输入原证照编号"
              clearable
              :style="{ width: '100%' }"
            >
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="取水权人名称或联系信息变更" prop="IF_INFO_CHA">
            <el-radio-group v-model="formData.IF_INFO_CHA" size="small">
              <el-radio
                v-for="(item, index) in hasOptions"
                :key="index"
                border
                :label="item.value"
                :disabled="item.disabled"
                >{{ item.label }}</el-radio
              >
            </el-radio-group>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="取水权变更" prop="IF_OWNER_CHA">
            <el-radio-group v-model="formData.IF_OWNER_CHA" size="small">
              <el-radio
                v-for="(item, index) in hasOptions"
                :key="index"
                border
                :label="item.value"
                :disabled="item.disabled"
                >{{ item.label }}</el-radio
              >
            </el-radio-group>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="法定身份证明文件" prop="IF_LEG_ID">
            <el-radio-group v-model="formData.IF_LEG_ID" size="small">
              <el-radio
                v-for="(item, index) in hasOptions"
                :key="index"
                border
                :label="item.value"
                :disabled="item.disabled"
                >{{ item.label }}</el-radio
              >
            </el-radio-group>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="上传法定身份证明文件" prop="LEG_ID_FILE_DES">
            <el-upload
              ref="LEG_ID_FILE_DES"
              :file-list="formData.LEG_ID_FILE_DES"
              action="/"
              :on-change="onChange1"
              :before-upload="beforeUpload"
            >
              <el-button size="small" type="primary" icon="el-icon-upload"
                >点击上传</el-button
              >
            </el-upload>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="取水权转让的批准文件" prop="IF_APP_DOC">
            <el-radio-group v-model="formData.IF_APP_DOC" size="small">
              <el-radio
                v-for="(item, index) in hasOptions"
                :key="index"
                border
                :label="item.value"
                :disabled="item.disabled"
                >{{ item.label }}</el-radio
              >
            </el-radio-group>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item
            label="上传取水权转让的批准文件"
            prop="APP_DOC_FILE_DES"
          >
            <el-upload
              ref="APP_DOC_FILE_DES"
              :file-list="formData.APP_DOC_FILE_DES"
              action="/"
              :on-change="onChange2"
              :before-upload="beforeUpload"
            >
              <el-button size="small" type="primary" icon="el-icon-upload"
                >点击上传</el-button
              >
            </el-upload>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="其他文件" prop="IF_OTHER_DOC">
            <el-radio-group v-model="formData.IF_OTHER_DOC" size="small">
              <el-radio
                v-for="(item, index) in hasOptions"
                :key="index"
                :label="item.value"
                border
                :disabled="item.disabled"
                >{{ item.label }}</el-radio
              >
            </el-radio-group>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="上传其他文件" prop="OTHER_DOC_FILE_DES">
            <el-upload
              ref="OTHER_DOC_FILE_DES"
              :file-list="formData.OTHER_DOC_FILE_DES"
              action="/"
              :on-change="onChange3"
              :before-upload="beforeUpload"
            >
              <el-button size="small" type="primary" icon="el-icon-upload"
                >点击上传</el-button
              >
            </el-upload>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item size="large">
            <el-button type="primary" @click="submitForm">提交</el-button>
            <el-button @click="resetForm">重置</el-button>
          </el-form-item>
        </el-col>
      </el-form>
    </el-card>
  </div>
</template>
<script>
import axios from "axios";
import { mapGetters } from "vuex";
export default {
  components: {},
  data() {
    return {
      loading: false,
      matterId: "37bfce447dd14a7396047481872169a2",
      formData: {
        CERT_NUM_CODE: "",
        IF_INFO_CHA: false,
        IF_OWNER_CHA: false,
        IF_LEG_ID: false,
        LEG_ID_FILE_DES: [],
        IF_APP_DOC: false,
        APP_DOC_FILE_DES: [],
        IF_OTHER_DOC: false,
        OTHER_DOC_FILE_DES: [],
      },
      rules: {
        CERT_NUM_CODE: [],
        IF_INFO_CHA: [
          {
            required: true,
            message: "取水权人名称或联系信息变更不能为空",
            trigger: "change",
          },
        ],
        IF_OWNER_CHA: [
          {
            required: true,
            message: "取水权变更不能为空",
            trigger: "change",
          },
        ],
        IF_LEG_ID: [
          {
            required: true,
            message: "法定身份证明文件不能为空",
            trigger: "change",
          },
        ],
        IF_APP_DOC: [
          {
            required: true,
            message: "取水权转让的批准文件不能为空",
            trigger: "change",
          },
        ],
        IF_OTHER_DOC: [
          {
            required: true,
            message: "其他文件不能为空",
            trigger: "change",
          },
        ],
      },
      uploadAction: "http://58.42.237.174:8088/group1/upload",
      hasOptions: [
        {
          label: "有",
          value: true,
        },
        {
          label: "无",
          value: false,
        },
      ],
    };
  },
  computed: {
    ...mapGetters({
      allPageInfo: "waterForm/allPageInfo",
    }),
  },
  watch: {},
  created() {},
  mounted() {
    axios
      .get(
        `http://58.42.237.177:8975/pdcApi/fillIn/getChangeInfo?matterId=${this.matterId}`
      )
      .then((res) => {
        console.log(res.data, "change");
        // this.formData = res.data.data;
      });
  },
  methods: {
    submitForm() {
      console.log(this.formData, "submitparams");
      this.$refs["elForm"].validate((valid) => {
        if (!valid) return;
        this.loading = true;
        const params = {
          CERT_NUM_CODE: this.formData.CERT_NUM_CODE,
          EVI_FILES: this.formData,
          IF_INFO_CHA: this.formData.IF_INFO_CHA,
          IF_OWNER_CHA: this.formData.IF_OWNER_CHA,
          matterId: this.formData.matterId,
        };
        axios
          .post(
            `http://58.42.237.177:8975/pdcApi/fillIn/saveChangeInfo`,
            params
          )
          .then((res) => {
            if (res.data.success) {
              this.$message.success("保存成功");
            } else {
              this.$message.error(res.data.message);
            }
          })
          .finally(() => {
            this.loading = false;
          });
      });
    },
    resetForm() {
      this.$refs["elForm"].resetFields();
    },
    onSuccessUploadDes(res, file) {
      console.log(file, 'file');
      // const fileContent = {
      //   FILE_NAME: '',
      //   FILE_EXT: '',
      //   FILE_DATA: '',
      //   WATER_CHANGE_APPLY_ID: '',
      //   TYPE: 1,
      // }
      // this.formData.LEG_ID_FILE_DES.push()
    },
    onChange1(file) {
      let reader = new FileReader();
      reader.readAsDataURL(file.raw);
      reader.onload = () => {
        const fileContent = {
        FILE_NAME: file.name,
        FILE_EXT: '',
        FILE_DATA: reader.result,
        WATER_CHANGE_APPLY_ID: '',
        TYPE: 1,// 1-法定身份证明文件 2-取水权转让的批准文件 3- 其他文件
        status: 'success',
        name: file.name,
      }
        this.formData.LEG_ID_FILE_DES.push(fileContent)
      };
    },
    onChange2(file) {
      let reader = new FileReader();
      reader.readAsDataURL(file.raw);
      reader.onload = () => {
        const fileContent = {
        FILE_NAME: file.name,
        FILE_EXT: '',
        FILE_DATA: reader.result,
        WATER_CHANGE_APPLY_ID: '',
        TYPE: 2,// 1-法定身份证明文件 2-取水权转让的批准文件 3- 其他文件
        status: 'success',
        name: file.name,
      }
        this.formData.APP_DOC_FILE_DES.push(fileContent)
      };
    },
    onChange3(file) {
      let reader = new FileReader();
      reader.readAsDataURL(file.raw);
      reader.onload = () => {
        const fileContent = {
        FILE_NAME: file.name,
        FILE_EXT: '',
        FILE_DATA: reader.result,
        WATER_CHANGE_APPLY_ID: '',
        TYPE: 3,// 1-法定身份证明文件 2-取水权转让的批准文件 3- 其他文件
        status: 'success',
        name: file.name,
      }
        this.formData.OTHER_DOC_FILE_DES.push(fileContent)
      };
    },
    beforeUpload(file) {
      let isRightSize = file.size / 1024 / 1024 < 2;
      if (!isRightSize) {
        this.$message.error("文件大小超过 2MB");
      }
        return false
      // return isRightSize;
    },
  },
};
</script>
<style>
.el-upload__tip {
  line-height: 1.2;
}
</style>
