<template>
  <div>
    <el-card>
      <div slot="header">
        <el-alert
          title="步骤8（论证报告信息）"
          type="info"
          center
          show-icon
          :closable="false"
        >
        </el-alert>
      </div>
      <el-form
        ref="elForm"
        :model="formData"
        size="medium"
        label-width="130px"
      >
        <el-row>
          <el-col :span="12">
            <el-form-item label="报告书名称" prop="REP_NAME">
              <el-input
                v-model="formData.REP_NAME"
                clearable
                :style="{ width: '100%' }"
              >
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="报告编制单位" prop="COMPL_UNIT">
              <el-input
                v-model="formData.COMPL_UNIT"
                clearable
                :style="{ width: '100%' }"
              >
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="报告编制单位代码" prop="COMPL_UNIT_CODE">
              <el-input
                v-model="formData.COMPL_UNIT_CODE"
                placeholder="请输入单位组织机构代码"
                clearable
                :style="{ width: '100%' }"
              >
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="编制负责人" prop="COMPL_MGR">
              <el-input
                v-model="formData.COMPL_MGR"
                clearable
                :style="{ width: '100%' }"
              >
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="编制负责人代码" prop="COMPL_MGR_CODE">
              <el-input
                v-model="formData.COMPL_MGR_CODE"
                placeholder="请输入身份证号码"
                clearable
                :style="{ width: '100%' }"
              >
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="编制负责人电话" prop="COMPL_MGR_PHONE">
              <el-input
                v-model="formData.COMPL_MGR_PHONE"
                clearable
                :style="{ width: '100%' }"
              >
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item size="large" style="text-align: center">
          <el-button type="primary" @click="submitForm">保存</el-button>
          <el-button @click="resetForm">重置</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>
<script>
import axios from "axios";
import { mapGetters } from "vuex";
export default {
  components: {},
  data() {
    return {
      loading: false,
      formData: {
        REP_NAME: "",
        COMPL_UNIT: "",
        COMPL_UNIT_CODE: "",
        COMPL_MGR: "",
        COMPL_MGR_CODE: "",
        COMPL_MGR_PHONE: "",
      },
    };
  },
  computed: {
    ...mapGetters({
      allPageInfo: "waterForm/allPageInfo",
    }),
  },
  watch: {},
  created() {},
  mounted() {
    // console.log(this.allPageInfo);
    this.formData = JSON.parse(JSON.stringify(this.allPageInfo?.EC_B_REPORT));
  },
  methods: {
    submitForm() {
      this.$refs["elForm"].validate((valid) => {
        if (!valid) return;
        this.loading = true;
        axios
          .post(
            `http://58.42.237.177:8975/pdcApi/fillIn/saveEcBReport`,
            this.formData
          )
          .then(res => {
            if(res.data.success) {
              this.$message.success("保存成功");
            } else {
              this.$message.error(res.data.message);
            }
          })
          .finally(() => {
            this.loading = false;
          });
      });
    },
    resetForm() {
      this.$refs["elForm"].resetFields();
    },
  },
};
</script>
<style></style>
