          </el-form-item>
          <el-form-item label="特写图片" prop="PHOTO_CLS">
            <el-upload
              class="avatar-uploader"
              action="http://58.42.237.174:8088/group1/upload"
              :show-file-list="false"
              :on-success="handleAvatarSuccess2"
              :before-upload="beforeUpload"
            >
              <img
                v-if="CLS_LOCAL_IMG"
                :src="CLS_LOCAL_IMG"
                class="avatar"
              />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
