<script>
import { mapGetters } from "vuex";
export default {
  components: {},
  data() {
    return {
      dialogVisible: true,
      FULL_LOCAL_IMG: "",
      CLS_LOCAL_IMG: "",
      limit: 1,
      formData: {
        MEAS_CON_CODE: "",
        MEAS_TYPE_CODE: "",
        TRANS_MODE_CODE: "",
        TRANS_LEVEL_CODE: [],
        TRANS_DEPT_CODE: [],
        MEAS_EQU_CODE: [],
        MEAS_SER_CODE: "",
        MEAS_MOD: "",
        WAT_ENG_INSP_DATE: "",
        COR_REQ: "",
        PHOTO_FULL: "",
        PHOTO_CLS: "",
      },
      rules: {
        MEAS_CON_CODE: [
          {
            required: true,
            message: "请选择",
            trigger: "change",
          },
        ],
        MEAS_TYPE_CODE: [
          {
            required: true,
            message: "请选择",
            trigger: "change",
          },
        ],
        TRANS_MODE_CODE: [
          {
            required: true,
            message: "请选择",
            trigger: "change",
          },
        ],
        TRANS_LEVEL_CODE: [
          {
            required: true,
            message: "请选择",
            trigger: "change",
          },
        ],
        TRANS_DEPT_CODE: [
          {
            required: true,
            message: "请选择",
            trigger: "change",
          },
        ],
        MEAS_EQU_CODE: [
          {
            required: true,
            message: "请选择",
            trigger: "change",
          },
        ],
        MEAS_SER_CODE: [
          {
            required: true,
            message: "请输入",
            trigger: "blur",
          },
        ],
        MEAS_MOD: [
          {
            required: true,
            message: "请输入",
            trigger: "blur",
          },
        ],
        WAT_ENG_INSP_DATE: [
          {
            required: true,
            message: "请选择",
            trigger: "change",
          },
        ],
        COR_REQ: [
          {
            required: true,
            message: "请输入",
            trigger: "blur",
          },
        ],
        PHOTO_FULL: [
          {
            required: true,
            message: "请选择",
            trigger: "blur",
          },
        ],
        PHOTO_CLS: [
          {
            required: true,
            message: "请选择",
            trigger: "blur",
          },
        ],
      },
    };
  },
  computed: {
    ...mapGetters({
      allDictData: "waterForm/allDictData",
    }),
  },
  watch: {},
  created() {},
  mounted() {},
  methods: {
    onOpen() {},
    onClose() {
      // this.$refs["elForm"].resetFields();
      this.$emit("cancel");
    },
    close() {
      this.$emit("cancel");
    },
    handelConfirm() {
      this.$refs["elForm"].validate((valid) => {
        if (!valid) return;
        this.formData.FULL_LOCAL_IMG = this.FULL_LOCAL_IMG;
        this.formData.CLS_LOCAL_IMG = this.CLS_LOCAL_IMG;
        this.$emit("confirm", this.formData);
      });
    },
    onChange1(file) {
      let reader = new FileReader();
      reader.readAsDataURL(file.raw);
      reader.onload = () => {
        this.formData.PHOTO_FULL = reader.result;
      };
    },
    onChange2(file) {
      let reader = new FileReader();
      reader.readAsDataURL(file.raw);
      reader.onload = () => {
        this.formData.PHOTO_CLS = reader.result;
      };
    },
    beforeUpload(file) {
      let isRightSize = file.size / 1024 <= 800;
      if (!isRightSize) {
        this.$message.error("文件大小不能超过 800KB");
      }
      return isRightSize;
    },
    handleAvatarSuccess1(res, file) {
      console.log(res, 'res');
      this.FULL_LOCAL_IMG = URL.createObjectURL(file.raw);
      this.formData.PHOTO_FULL = res;
    },
    handleAvatarSuccess2(res, file) {
      console.log(res, 'res');
      this.CLS_LOCAL_IMG = URL.createObjectURL(file.raw);
      this.formData.PHOTO_CLS = res;
    },
  },
};
</script>
