            console.log(res, "res");
            if (res.data.success) {
              this.$message.success("保存成功");
            } else {
              this.$message.error(res.data.message);
            }
          })
          .finally(() => {
            this.loading = false;
          });
      });
    },
    resetForm() {
      this.$refs["elForm"].resetFields();
    },
  },
};
</script>
