        IF_MEA_FAC_CHK: false,
        MEA_FAC_CHK_DEPT: "",
        LAST_CHK_DATE: "",
        MEA_FAC_CARE: "",
      },
      formData: {
        MATTER_ID: "",
        CERT_NUM_CODE: "",
        tableData: [
          {
            tableText: "前五年各年取水量（万m3/年）",
            placeholder: "精确至两位小数",
          },
          {
            tableText: "前五年各年达产率（%）",
            placeholder: "精确至一位小数",
          },
        ],
      },
      rules: {
        CERT_NUM_CODE: [],
        IF_TECH_CHA: [
          {
            required: true,
            message: "主要生产用水技术、工艺、设备是否有变化不能为空",
            trigger: "change",
          },
        ],
        TECH_CHA: [],
        USAGE_CHA: [],
        IF_USAGE_CHA: [
          {
            required: true,
            message: "取水源、地点、取水方式水用途是否有变化不能为空",
            trigger: "change",
          },
        ],
        MEA_CHA: [],
        IF_MEA_CHA: [
          {
            required: true,
            message: "计量位置的拆改挪移或计量方式的调整不能为空",
            trigger: "change",
          },
        ],
        IF_ALL_MEA: [
          {
            required: true,
            message: "对全部取用水进行了有效计量不能为空",
            trigger: "change",
          },
        ],
        IF_MEA_FAC_CHK: [
          {
            required: true,
            message: "计量设施定期进行检验或校准不能为空",
            trigger: "change",
          },
        ],
        MEA_FAC_CHK_DEPT: [],
        LAST_CHK_DATE: [],
        MEA_FAC_CARE: [
          {
            required: true,
            message: "计量设施的维护方式不能为空",
            trigger: "change",
          },
        ],
      },
      hasOptions: [
        {
          label: "有",
          value: true,
        },
        {
          label: "无",
          value: false,
        },
      ],
      wayLists: [
        {
          label: "自维护",
          value: 1,
        },
        {
          label: "第三方维护",
          value: 2,
        },
        {
          label: "政府组织维护",
          value: 3,
        },
      ],
    };
  },
  computed: {
    ...mapGetters({
      allPageInfo: "waterForm/allPageInfo",
    }),
  },
  watch: {},
  created() {},
  mounted() {
    axios
      .get(
        `http://58.42.237.177:8975/pdcApi/fillIn/getRenInfo?matterId=${this.matterId}`
      )
      .then((res) => {
        console.log(res.data, "continue");
        // this.formData = res.data.data;
      });
  },
  methods: {
    submitForm() {
      this.$refs["elForm"].validate((valid) => {
        if (!valid) return;
        this.loading = true;
        const FIVE_YEAR_INT = [];
        const FIVE_YEAR_IDX = [];
        console.log(this.formData, "表单内容");
        this.formData.tableData.forEach((v, i) => {
          Object.keys(v).forEach((item) => {
            if (item.includes("getValue")) {
              if (i === 0) {
                FIVE_YEAR_INT.push(+v[item]);
              } else {
                FIVE_YEAR_IDX.push(+v[item]);
              }
            }
          });
        });
        const params = {
          MATTER_ID: this.allPageInfo?.MATTER_ID,
          CERT_NUM_CODE: this.formData.CERT_NUM_CODE,
          EC_CT_INT_INFO: {
            FIVE_YEAR_IDX,
            FIVE_YEAR_INT,
            ...this.EC_CT_INT_INFO,
          },
        };
        axios
          .post(
            `http://58.42.237.177:8975/pdcApi/fillIn/saveRenewalInfo`,
            params
          )
          .then(res => {
            if(res.data.success) {
              this.$message.success("保存成功");
            } else {
              this.$message.error(res.data.message);
            }
          })
          .finally(() => {
            this.loading = false;
          });
      });
    },
    resetForm() {
      this.$refs["elForm"].resetFields();
    },
  },
};
</script>
