<template>
  <div>
    <el-card>
      <div slot="header">
        <el-alert
          title="步骤6（退水监督）"
          type="info"
          center
          show-icon
          :closable="false"
        >
        </el-alert>
      </div>
      <el-table
        :data="tableData"
        border
        :header-cell-style="{ background: '#F3F4F7', color: '#555' }"
      >
        <el-table-column
          type="index"
          width="100px"
          label="退水口编号"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="BRK_TYPE_CODE"
          label="类型"
          width="150px"
          align="center"
        >
          <template slot-scope="scope">
            <el-select
              v-model="scope.row.BRK_TYPE_CODE"
              placeholder="请选择"
              clearable
              :style="{ width: '100%' }"
            >
              <el-option
                v-for="(item, index) in allDictData.BRK_TYPE"
                :key="index"
                :label="item.name"
                :value="item.code"
              ></el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column
          prop="BRK_TYPE"
          label="具体退水去向"
          width="150px"
          align="center"
        >
          <template slot-scope="scope">
            <el-input v-model="scope.row.BRK_TYPE"></el-input>
          </template>
        </el-table-column>
        <el-table-column
          prop="BRK_LOC"
          label="退水地点"
          width="150px"
          align="center"
        >
          <template slot-scope="scope">
            <el-input v-model="scope.row.BRK_LOC"></el-input>
          </template>
        </el-table-column>
        <el-table-column
          prop="MNT_TP_CODE"
          label="监测方式"
          width="150px"
          align="center"
        >
          <template slot-scope="scope">
            <el-select
              v-model="scope.row.MNT_TP_CODE"
              placeholder="请选择"
              clearable
              :style="{ width: '100%' }"
            >
              <el-option
                v-for="(item, index) in MNT_TP_LIST"
                :key="index"
                :label="item.name"
                :value="item.code"
              ></el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column
          prop="MNT_TP"
          label="具体监测方式"
          width="150px"
          align="center"
        >
          <template slot-scope="scope">
            <el-input v-model="scope.row.MNT_TP"></el-input>
          </template>
        </el-table-column>
        <el-table-column
          prop="BRK_AMT"
          label="退水量"
          width="180px"
          align="center"
        >
          <template slot-scope="scope">
            <el-input v-model="scope.row.BRK_AMT"
              ><template slot="append">万m³/年</template></el-input
            >
          </template>
        </el-table-column>
        <el-table-column
          prop="BRK_QUA_IDX"
          label="退水水质执行标准"
          width="150px"
          align="center"
        >
          <template slot-scope="scope">
            <el-input v-model="scope.row.BRK_QUA_IDX"></el-input>
          </template>
        </el-table-column>
        <el-table-column
          prop="POLL_TP"
          label="主要污染物种类"
          width="150px"
          align="center"
        >
          <template slot-scope="scope">
            <el-input v-model="scope.row.POLL_TP"></el-input>
          </template>
        </el-table-column>
        <el-table-column
          prop="BRK_ZONE"
          label="退水水功能区名称"
          width="150px"
          align="center"
        >
          <template slot-scope="scope">
            <el-input v-model="scope.row.BRK_ZONE"></el-input>
          </template>
        </el-table-column>
        <el-table-column
          prop="OTHER_INFO"
          label="其它信息"
          width="150px"
          align="center"
        >
          <template slot-scope="scope">
            <el-input v-model="scope.row.OTHER_INFO"></el-input>
          </template>
        </el-table-column>
        <el-table-column fixed="right" width="150" label="操作" align="center">
          <template slot-scope="scope">
            <el-button
              @click="deleteRow(scope.$index, tableData)"
              type="text"
              size="small"
            >
              <i class="el-icon-delete"></i>
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="text-align: center;margin-top:10px">
        <el-button type="primary" size="mini" @click="onAddTableData"
          ><i class="el-icon-plus"></i>再加一个退水口信息</el-button
        >
      </div>
      <div style="text-align: center;margin-top:10px">
        <el-button
          type="primary"
          size="mini"
          @click="submitForm"
          :loading="loading"
          >保存</el-button
        >
      </div>
    </el-card>
  </div>
</template>

<script>
import axios from "axios";
import { mapGetters } from "vuex";
export default {
  name: "step6",
  components: {},
  data() {
    return {
      loading: false,
      tableData: [],
      MNT_TP_LIST: [
        {
          code: "1",
          name: "监测",
        },
        {
          code: "2",
          name: "推算",
        },
        {
          code: "3",
          name: "其他",
        },
      ],
    };
  },
  watch: {},
  computed: {
    ...mapGetters({
      allPageInfo: "waterForm/allPageInfo",
      allDictData: "waterForm/allDictData",
    }),
  },
  mounted() {
    console.log(this.allPageInfo);
    this.tableData = JSON.parse(JSON.stringify(this.allPageInfo?.EC_B_WT_BRK));
  },
  methods: {
    submitForm() {
      this.loading = true;
      // this.formData.MATTER_ID = this.allPageInfo.MATTER_ID;
      axios
        .post(
          `http://58.42.237.177:8975/pdcApi/fillIn/saveEcBWtBrk`,
          this.tableData
        )
        .then((res) => {
          if (res.data.success) {
            this.$message.success("保存成功");
          } else {
            this.$message.error(res.data.message);
          }
        })
        .finally(() => {
          this.loading = false;
        });
    },
    onAddTableData() {
      if (!this.tableData) {
        this.tableData = [];
      }
      this.tableData.push({
        EC_ID: this.allPageInfo.ID,
        BRK_TYPE: "",
        BRK_TYPE_CODE: "",
        BRK_LOC: "",
        MNT_TP: "",
        MNT_TP_CODE: "",
        BRK_AMT: "",
        BRK_QUA_IDX: "",
        POLL_TP: "",
        BRK_ZONE: "",
        OTHER_INFO: "",
      });
    },
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },
  },
};
</script>
<style lang="scss" scoped></style>
